<template>
	<gui-page customHeader="true">
		<view slot="gHeader">
			<view style="display: flex; justify-content:center; align-items:center;">
				<text class="pageTitle" style="font: inter; font-size:36rpx; font-weight:600; color:#333333;">资讯</text>
			</view>
		</view>
		<view slot="gBody" class="newsContainer">
			<view class="gui-flex gui-justify-content-center gui-align-items-center" style="background-color:#F0F0F0;">
				<gui-search placeholder="请输入关键词搜索"
					style="background-color: #ffffff; width:650rpx; padding: 8rpx; margin:30rpx; letter-spacing:5rpx; border-radius:10rpx;">
				</gui-search>
			</view>
			<view>
				<view>
					<view class="gui-flex gui-justify-content-center" style="position: relative;">
						<image :src="recommend.image" class="imageSize" mode="" @click="showNewsList()"></image>
						<view style="position: absolute; left:50rpx; top:300rpx;">
							<text style="font-size: 12rpx; font-weight:400; color:#FFFFFF;">{{recommend.title}}</text>
						</view>
					</view>
				</view>
				<view v-for="(item,index) in news" :key="index">

					<view class="faqContainer">
						<text class="FAQText">{{item.title}}</text>
						<text class="seeMoreText">查看更多</text>
					</view>
					<view class="problem1Container" v-for="(list,ind) in item.list" :key="ind">
						<Problem1 :data="list" />
					</view>
				</view>
			</view>

		</view>
	</gui-page>
</template>

<script>
	import request from "@/common/request.js"
	import Problem1 from "@/pages/News/Problem1.vue"
	import CompanyPolicy from "@/pages/News/CompanyPolicy.vue"
	import News2 from "@/pages/News/News2.vue"
	export default {
		data() {
			return {
				news: [],
				recommend: {},
	
			}
		},

		onShow: async function() {
			try{
				const response = await request.news({})
				this.news = response.data.data.list;
				this.recommend = response.data.data.recommend
				console.log(response.data.data)
			} catch (error) {
				console.log(error)
			}
		
		},
		methods: {
			showNewsList: async function() {
				console.log("clicked")
			},
		},
		components: {
			Problem1,
			CompanyPolicy,
			News2
		}
	}
</script>

<style scoped>
	.imageSize {
		width: 100%;
		height: 900rpx;
	}

	.faqContainer {
		padding: 25rpx;
		display: flex;
		justify-content: space-between;
		text-align: center;
		align-items: center;
	}

	.FAQText {
		color: #333333;
		Font-family: Inter;
		Font-style: Regular;
		Font-size: 42rpx;
	}

	.seeMoreText {
		color: #999999;
		Font-family: Inter;
		Font-style: Regular;
		Font-size: 28rpx;
	}

	.newsContainer {
		background-color: #F0F0F0;
	}

	.companyPolicyContainer,
	.news2Container {
		background-color: #FFFFFF;
	}

	.faqContainer,
	.problem1Container {
		background-color: #FFFFFF;

	}
</style>
